.reservations-list {
	background: white;
	border-radius: 10px;
	padding: 20px;
	box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
	margin: 20px 0;
}

.reservations-list.empty {
	text-align: center;
	color: #666;
	padding: 40px 20px;
}

.reservations-list h3 {
	margin-top: 0;
	color: #256068;
	border-bottom: 2px solid #eee;
	padding-bottom: 10px;
}

.reservation-item {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 15px;
	border-bottom: 1px solid #eee;
	transition: background-color 0.2s;
}

.reservation-item:hover {
	background-color: #f9f9f9;
}

.reservation-item:last-child {
	border-bottom: none;
}

.reservation-info {
	display: flex;
	flex-direction: column;
	gap: 5px;
}

.space-number {
	font-weight: bold;
	color: #256068;
	font-size: 16px;
}

.reservation-time {
	color: #666;
	font-size: 14px;
}

.reservation-duration {
	color: #888;
	font-size: 13px;
}

.cancel-button {
	padding: 8px 16px;
	background-color: #f44336;
	color: white;
	border: none;
	border-radius: 4px;
	cursor: pointer;
	font-size: 14px;
	transition: background-color 0.2s;
}

.cancel-button:hover {
	background-color: #d32f2f;
}

.reservation-actions {
	display: flex;
	gap: 10px;
}

.pay-button {
	padding: 8px 16px;
	background-color: #4caf50;
	color: white;
	border: none;
	border-radius: 4px;
	cursor: pointer;
	font-size: 14px;
	transition: background-color 0.2s;
}

.pay-button:hover {
	background-color: #388e3c;
}
