.parking-map {
	margin: 20px 0;
	padding: 20px;
	background-color: #34495e;
	border: 3px solid #636e72;
	border-radius: 10px;
	box-shadow: 0 6px 10px rgba(0, 0, 0, 0.15);
}

.row {
	display: flex;
	justify-content: center;
	margin-bottom: 15px;
	gap: 15px;
}

.row:last-child {
	margin-bottom: 0;
}

.row-1 {
	margin-left: 4vw;
	margin-right: 4vw;
}

.row-2 {
	margin-left: 4vw;
	margin-right: 4vw;
}

.row-3 {
	margin-left: 4vw;
	margin-right: 4vw;
}

.row-4 {
	margin-left: 4vw;
	margin-right: 4vw;
}

.space {
	width: 60px;
	height: 40px;
	display: flex;
	align-items: center;
	justify-content: center;
	border-radius: 5px;
	cursor: pointer;
	font-weight: bold;
	font-size: 14px;
	transition: all 0.3s ease;
	user-select: none;
	border: 2px solid;
}

.space:hover {
	transform: translateY(-2px);
	box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
}

/* Зеленый - свободные места */
.space.free {
	background-color: #517c47 !important; /* Ваш зеленый */
	color: white;
	border-color: #2d3d26 !important;
}

/* Красный - занятые места */
.space.occupied {
	background-color: #c0392b !important; /* Ваш красный */
	color: white;
	border-color: #4e1d19 !important;
}

/* Коричневый - забронированные места */
.space.reserved {
	background-color: #a37935 !important; /* Ваш коричневый */
	color: white;
	border-color: #633710 !important;
	animation: pulse 1.5s infinite;
	position: relative;
}

.space.reserved::after {
	content: '★';
	position: absolute;
	top: -5px;
	right: -5px;
	font-size: 12px;
	color: gold;
}

/* Прозрачные - для счетчиков */
.space.transparent {
	background-color: transparent;
	border: 2px dashed #ccc;
	color: #333;
	cursor: default;
}

.space.transparent:hover {
	transform: none;
	box-shadow: none;
}

@keyframes pulse {
	0%,
	100% {
		opacity: 1;
		box-shadow: 0 0 5px rgba(163, 121, 53, 0.5);
	}
	50% {
		opacity: 0.9;
		box-shadow: 0 0 10px rgba(163, 121, 53, 0.8);
	}
}

/* Стили для разных рядов (как в вашем примере) */
.row-1 .space {
	background-color: #e3f2fd;
}

.row-2 .space {
	background-color: #f3e5f5;
}

.row-3 .space {
	background-color: #e8f5e9;
}

.row-4 .space {
	background-color: #fff3e0;
}

/* Responsive */
@media (max-width: 1024px) {
	.space {
		width: 50px;
		height: 35px;
		font-size: 13px;
	}
}

@media (max-width: 768px) {
	.space {
		width: 40px;
		height: 30px;
		font-size: 12px;
	}

	.row {
		gap: 10px;
		margin-bottom: 10px;
	}
}

@media (max-width: 480px) {
	.space {
		width: 30px;
		height: 25px;
		font-size: 10px;
	}

	.row {
		gap: 8px;
		margin-bottom: 8px;
	}
}
