.app-footer {
	background: #2c3e50;
	color: white;
	padding: 20px;
	text-align: center;
	margin-top: 40px;
}

.app-footer p {
	margin: 10px 0;
}

.app-footer a {
	color: #4fc3f7;
	text-decoration: none;
	transition: color 0.2s;
}

.app-footer a:hover {
	color: #29b6f6;
	text-decoration: underline;
}

.footer-version {
	font-size: 0.9rem;
	opacity: 0.7;
}
