.stats-panel {
	background: white;
	border-radius: 10px;
	padding: 20px;
	box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
	margin: 20px 0;
}

.stats-grid {
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	gap: 15px;
	margin-bottom: 20px;
}

.stat-item {
	text-align: center;
	padding: 15px;
	border-radius: 8px;
	background-color: #f8f9fa;
}

.stat-label {
	font-size: 14px;
	color: #666;
	margin-bottom: 5px;
}

.stat-value {
	font-size: 24px;
	font-weight: bold;
}

.stat-value.total {
	color: #2196f3;
}
.stat-value.free {
	color: #4caf50;
}
.stat-value.occupied {
	color: #f44336;
}
.stat-value.reserved {
	color: #ff9800;
}

.time-display {
	text-align: center;
	padding: 15px;
	background-color: #256068;
	color: white;
	border-radius: 8px;
}

.time-label {
	font-size: 14px;
	opacity: 0.9;
	margin-bottom: 5px;
}

.time-value {
	font-size: 24px;
	font-weight: bold;
	font-family: 'Courier New', monospace;
}
